html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 0.69em;
  height: 1em;
  fill: #FFF;
}

.icon-check {
  width: 1.23em;
  height: 1em;
  fill: #E8663C;
}

.icon-cog {
  width: 1em;
  height: 1em;
  fill: #E8663C;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-user {
  width: 1.05em;
  height: 1em;
  fill: #E8663C;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

@font-face {
  font-family: "bebas-font";
  src: url("../fonts/bebas.woff2") format("woff2"), url("../fonts/bebas.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: Helvetica, sans-serif;
}

.button {
  font-size: 12px;
  text-transform: uppercase;
  height: 31px;
  padding: 0 13px;
  display: inline-block;
  color: #ffffff;
  text-decoration: none;
  background-color: #e8603c;
  line-height: 24px;
  font-family: bebas, sans-serif;
}

.h3 {
  display: flex;
  font-size: 18px;
  color: #e8603c;
  text-transform: uppercase;
  text-decoration: none;
}

.h3:after {
  content: "";
  flex: 1;
  margin-left: 20px;
  background: url(../img/pattern.png) 0 0;
}

.block {
  display: flex;
}

.container {
  max-width: 940px;
  margin: 0 auto;
}

.header__wrap {
  display: flex;
  text-align: left;
  padding: 22px 0;
  align-items: baseline;
}

.header__nav {
  margin-left: auto;
}

.logo {
  font-size: 0;
  color: #e8603c;
  text-decoration: none;
  font-family: bebas, sans-serif;
}

.logo__part1, .logo__part2 {
  display: inline-block;
  font-size: 36px;
}

.logo__part2 {
  color: #f3af9d;
}

.nav {
  font-size: 16px;
  position: relative;
  top: -6px;
  font-family: bebas, sans-serif;
}

.nav a {
  text-decoration: none;
  color: #b9b9b9;
  margin-left: 24px;
}

.nav a.is-active, .nav a:hover {
  color: #e8603c;
}

.slider {
  position: relative;
}

.slider__slide {
  height: 0;
  padding-bottom: 42%;
}

.slider__next, .slider__prev {
  position: absolute;
  top: 45%;
  left: 3%;
  cursor: pointer;
  z-index: 100;
  font-size: 54px;
}

.slider__next {
  left: auto;
  right: 20px;
  transform: rotate(-180deg);
}

.hero {
  overflow: hidden;
}

.hero__wrap {
  position: relative;
}

.hero__wrap:before, .hero__wrap:after {
  position: absolute;
  height: 60px;
  background: #f2ae9c;
  content: "";
  width: 1000px;
}

.hero__wrap:before {
  right: 100%;
  margin-right: 10px;
}

.hero__wrap:after {
  left: 100%;
  margin-left: 10px;
}
